<template>
    <div class="message-detail">
        <navigate title="详情" position="absolute"/>
        <div class="scroll-wrapper">
            <div class="header border-1px-b">
                <h2 class="title">{{detail.title}}</h2>
                <p class="time">{{detail.publishTime}}</p>
            </div>
            <p class="content">
                {{detail.content}}
            </p>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {queryNoticeDetail} from "../../../api/personal"


    export default {
        name: "detail",
        data() {
            return {
                detail: {}
            }
        },
        props: {
            id: {
                type: String,
                default: ''
            }
        },
        created() {
            this.queryNoticeDetail()
        },
        methods: {
            queryNoticeDetail() {
                queryNoticeDetail({id: this.id}).then(res => {
                    this.detail = res
                })
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .message-detail
        position fixed
        top 0
        left 0
        right 0
        bottom 0
        z-index 1000
        padding-top 46px
        background-color #fff
        .scroll-wrapper
            width 100%
            height 100%
            overflow-x hidden
            overflow-y auto
            .header
                padding 10px 15px
                text-align center
                .title
                    line-height 2.33
                    font-size $font-size-lm
                .time
                    line-height 1.5
                    color #cecece
                    font-size $font-size-sm
            .content
                padding 10px 15px
                line-height 1.74625
                text-indent 2em
                font-size $font-size-mm

</style>



